html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "League Spartan", sans-serif;
  font-size: 0.9375rem;
  background-image: url(/images/bg-pattern-top-mobile.svg), url(/images/bg-pattern-bottom-mobile.svg);
  background-position: left top, right bottom;
  background-repeat: no-repeat;
  background-size: auto, contain;
}
@media (min-width: 56.25em) {
  body {
    background-size: auto, cover;
  }
}
@media (min-width: 64em) {
  body {
    background-image: url(/images/bg-pattern-top-desktop.svg), url(/images/bg-pattern-bottom-desktop.svg);
    background-size: auto, contain;
  }
}

.margin-left {
  margin-left: 3.125rem;
}
@media (max-width: 87.4375em) {
  .margin-left {
    margin: 0;
  }
}
.margin-left__big {
  margin-left: 6.25rem;
}
@media (max-width: 87.4375em) {
  .margin-left__big {
    margin: 0;
  }
}

.margin-right {
  margin-right: 3.125rem;
}
@media (max-width: 87.4375em) {
  .margin-right {
    margin: 0;
  }
}
.margin-right__big {
  margin-right: 6.25rem;
}
@media (max-width: 87.4375em) {
  .margin-right__big {
    margin: 0;
  }
}

.margin-top {
  margin-top: 1.875rem;
}
@media (max-width: 87.4375em) {
  .margin-top {
    margin: 0;
  }
}
.margin-top__big {
  margin-top: 3.75rem;
}
@media (max-width: 87.4375em) {
  .margin-top__big {
    margin: 0;
  }
}

.zoom-hover {
  transition: transform 150ms ease-in-out;
}

.zoom-hover:hover {
  transform: scale(1.03);
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

main {
  padding: 4.75rem 1.125rem;
  text-align: center;
  align-content: center;
}
@media (max-width: 87.4375em) {
  main {
    max-width: 62.5rem;
    margin: auto;
  }
}
@media (min-width: 87.5em) {
  main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    padding: 7.5rem 11.25rem;
    height: 100vh;
  }
}
main header {
  margin-bottom: 1.875rem;
}
@media (min-width: 87.5em) {
  main header {
    text-align: left;
  }
}
main header h1 {
  color: hsl(300deg, 43%, 22%);
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 0.8;
  margin-bottom: 1.625rem;
  max-width: 26.25rem;
  display: inline-block;
}
@media (min-width: 87.5em) {
  main header h1 {
    font-size: 3.8125rem;
  }
}
main header p {
  color: hsl(303deg, 10%, 53%);
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.2;
}
@media (min-width: 87.5em) {
  main header p {
    width: 80%;
  }
}
main .rating-section {
  margin-bottom: 3.125rem;
}
main .rating-section__container {
  background-color: hsl(300deg, 24%, 96%);
  padding: 1rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 87.5em) {
  main .rating-section__container {
    display: flex;
    align-items: center;
    padding-left: 1.75rem;
  }
}
main .rating-section__container:not(:last-child) {
  margin-bottom: 1rem;
}
main .rating-section__stars {
  margin-right: 1.5rem;
}
@media (max-width: 87.4375em) {
  main .rating-section__stars {
    margin-bottom: 1rem;
    margin-right: 0;
  }
}
main .rating-section__stars img:not(:last-child) {
  padding-right: 0.25rem;
}
main .rating-section p {
  font-size: 1.125rem;
  font-weight: 700;
  color: hsl(300deg, 43%, 22%);
}
main .reviews-section {
  text-align: left;
}
@media (min-width: 87.5em) {
  main .reviews-section {
    grid-column: span 2;
    display: flex;
    align-items: center;
  }
}
main .reviews-section__container {
  background-color: hsl(300deg, 43%, 22%);
  padding: 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 87.5em) {
  main .reviews-section__container {
    height: fit-content;
  }
  main .reviews-section__container:not(:last-child) {
    margin-right: 1.875rem;
  }
}
@media (max-width: 87.4375em) {
  main .reviews-section__container:not(:last-child) {
    margin-bottom: 1rem;
  }
}
main .reviews-section__header {
  display: flex;
  align-items: center;
  margin-bottom: 1.625rem;
}
main .reviews-section__profileimg {
  width: 2.5rem;
  border-radius: 1.25rem;
  margin-right: 1.375rem;
}
main .reviews-section__person {
  display: inline-block;
  font-size: 1rem;
}
main .reviews-section__person .name {
  color: white;
  font-weight: 500;
  margin-bottom: 0.25rem;
}
main .reviews-section__person .tag {
  color: hsl(333deg, 80%, 67%);
}
main .reviews-section__text {
  color: white;
  font-size: 1rem;
  line-height: 1.4;
}